<template>
  <el-table :data="list" style="width: 100%" :height="height">
    <el-table-column label="项目名称" min-width="100">
      <template slot-scope="{ row }">
        {{ row.ProjName }}
      </template>
    </el-table-column>
    <el-table-column label="考勤天数" width="76" align="center">
      <template slot-scope="{ row }"> {{ row.SupervisorCount }} </template>
    </el-table-column>
    <el-table-column label="缺卡次数" width="76" align="center">
      <template slot-scope="{ row }">
        {{ row.BuildingArea }}
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
import { fetchPageList } from "@/api/project";

export default {
  filters: {
    statusFilter(status) {
      const statusMap = {
        success: "success",
        pending: "danger",
      };
      return statusMap[status];
    },
    orderNoFilter(str) {
      return str.substring(0, 30);
    },
  },
  props: {
    dateType: {
      type: String,
      default: "本周",
    },
    className: {
      type: String,
      default: "chart",
    },
    width: {
      type: String,
      default: "100%",
    },
    height: {
      type: String,
      default: "350px",
    },
    autoResize: {
      type: Boolean,
      default: true,
    },
    chartData: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      list: null,
    };
  },
  watch: {
    dateType(newValue, oldValue) {
      this.fetchData();
    },
  },
  created() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      fetchPageList().then((response) => {
        this.list = response.Data;
      });
    },
  },
};
</script>
