<template>
  <el-table :data="list" style="width: 100%" :height="height">
    <el-table-column label="公司名称" min-width="100">
      <template slot-scope="{ row }">
        {{ row.CompanyName }}
      </template>
    </el-table-column>
    <el-table-column label="信用评分" width="76" align="center">
      <template slot-scope="{ row }">
        {{ row.SupervisorCount }}
      </template>
    </el-table-column>
  </el-table>
</template>

<script>
import { fetchList } from "@/api/company";

export default {
  filters: {
    statusFilter(status) {
      const statusMap = {
        success: "success",
        pending: "danger",
      };
      return statusMap[status];
    },
    orderNoFilter(str) {
      return str.substring(0, 30);
    },
  },
  props: {
    dateType: {
      type: String,
      default: "本周",
    },
    className: {
      type: String,
      default: "chart",
    },
    width: {
      type: String,
      default: "100%",
    },
    height: {
      type: String,
      default: "350px",
    },
    autoResize: {
      type: Boolean,
      default: true,
    },
    chartData: {
      type: Object,
      required: true,
    },
  },
  data() {
    return {
      list: null,
    };
  },
  watch: {
    dateType(newValue, oldValue) {
      this.fetchData();
    },
  },
  created() {
    this.fetchData();
  },
  methods: {
    fetchData() {
      fetchList().then((response) => {
        this.list = response.Data;
        console.log("list", this.list);
      });
    },
  },
};
</script>
